	module.exports = {"data":{"postsJson":{"title":"Last Supper: Servanthood & Identity","questionsTitle":"FOLLOWING GODLY EXAMPLES","display":"block","bookName":"John","ref":"13:1-14:31","html":"<p>   <strong>13</strong><u>1</u>Now before the feast of the Passover, Jesus, knowing that his time had come that he would depart from this world to the Father, having loved his own who were in the world, he loved them to the end. <u>2</u>During supper, the devil having already put into the heart of Judas Iscariot, Simon’s son, to betray him, <u>3</u>Jesus, knowing that the Father had given all things into his hands, and that he came from God, and was going to God, <u>4</u>arose from supper, and laid aside his outer garments. He took a towel, and wrapped a towel around his waist. <u>5</u>Then he poured water into the basin, and began to wash the disciples’ feet, and to wipe them with the towel that was wrapped around him. <u>6</u>Then he came to Simon Peter. He said to him,</p><h1>SIMON PETER</h1><h3>Lord, do you wash my feet?</h3><p>   <u>7</u>Jesus answered him,</p><h1>JESUS</h1><h2>You don’t know what I am doing now, but you will understand later.</h2><p>   <u>8</u>Peter said to him,</p><h1>SIMON PETER</h1><h3>You will never wash my feet!</h3><p>   Jesus answered him,</p><h1>JESUS</h1><h2>If I don’t wash you, you have no part with me.</h2><p>   <u>9</u>Simon Peter said to him,</p><h1>SIMON PETER</h1><h3>Lord, not my feet only, but also my hands and my head!</h3><p>   <u>10</u>Jesus said to him,</p><h1>JESUS</h1><h2>Someone who has bathed only needs to have his feet washed, but is completely clean. You are clean, but not all of you.</h2><p><u>11</u>For he knew him who would betray him, therefore he said,</p><h1>JESUS</h1><h2>You are not all clean.</h2><p><u>12</u>So when he had washed their feet, put his outer garment back on, and sat down again, he said to them,</p><h1>JESUS</h1><h2>Do you know what I have done to you? <u>13</u>You call me, ‘Teacher’ and ‘Lord.’ You say so correctly, for so I am. <u>14</u>If I then, the Lord and the Teacher, have washed your feet, you also ought to wash one another’s feet. <u>15</u>For I have given you an example, that you also should do as I have done to you. <u>16</u>Most certainly I tell you, a servant is not greater than his lord, neither one who is sent greater than he who sent him. <u>17</u>If you know these things, blessed are you if you do them. <u>18</u>I don’t speak concerning all of you. I know whom I have chosen. But that the Scripture may be fulfilled, ‘He who eats bread with me has lifted up his heel against me.’* <u>19</u>From now on, I tell you before it happens, that when it happens, you may believe that I am he. <u>20</u>Most certainly I tell you, he who receives whomever I send, receives me; and he who receives me, receives him who sent me.</h2><p>   <u>21</u>When Jesus had said this, he was troubled in spirit, and testified,</p><h1>JESUS</h1><h2>Most certainly I tell you that one of you will betray me.</h2><p>   <u>22</u>The disciples looked at one another, perplexed about whom he spoke. <u>23</u>One of his disciples, whom Jesus loved, was at the table, leaning against Jesus’ breast. <u>24</u>Simon Peter therefore beckoned to him, and said to him,</p><h1>SIMON PETER</h1><h3>Tell us who it is of whom he speaks.</h3><p>   <u>25</u>He, leaning back, as he was, on Jesus’ breast, asked him,</p><h1>JOHN THE DISCIPLE</h1><h3>Lord, who is it?</h3><p>   <u>26</u>Jesus therefore answered,</p><h1>JESUS</h1><h2>It is he to whom I will give this piece of bread when I have dipped it.</h2><p>So when he had dipped the piece of bread, he gave it to Judas, the son of Simon Iscariot. <u>27</u>After the piece of bread, then Satan entered into him. <br />   Then Jesus said to him,</p><h1>JESUS</h1><h2>What you do, do quickly.</h2><p>   <u>28</u>Now no man at the table knew why he said this to him. <u>29</u>For some thought, because Judas had the money box, that Jesus said to him, “Buy what things we need for the feast,” or that he should give something to the poor. <u>30</u>Therefore having received that morsel, he went out immediately. It was night. <br />   <u>31</u>When he had gone out, Jesus said,</p><h1>JESUS</h1><h2>Now the Son of Man has been glorified, and God has been glorified in him. <u>32</u>If God has been glorified in him, God will also glorify him in himself, and he will glorify him immediately. <u>33</u>Little children, I will be with you a little while longer. You will seek me, and as I said to the Jews, ‘Where I am going, you can’t come,’ so now I tell you. <u>34</u>A new commandment I give to you, that you love one another. Just as I have loved you, you also love one another. <u>35</u>By this everyone will know that you are my disciples, if you have love for one another.</h2><p>   <u>36</u>Simon Peter said to him,</p><h1>SIMON PETER</h1><h3>Lord, where are you going?</h3><p>   Jesus answered,</p><h1>JESUS</h1><h2>Where I am going, you can’t follow now, but you will follow afterwards.</h2><p>   <u>37</u>Peter said to him,</p><h1>SIMON PETER</h1><h3>Lord, why can’t I follow you now? I will lay down my life for you.</h3><p>   <u>38</u>Jesus answered him,</p><h1>JESUS</h1><h2>Will you lay down your life for me? Most certainly I tell you, the rooster won’t crow until you have denied me three times. <br />   <strong>14</strong><u>1</u>Don’t let your heart be troubled. Believe in God. Believe also in me. <u>2</u>In my Father’s house are many homes. If it weren’t so, I would have told you. I am going to prepare a place for you. <u>3</u>If I go and prepare a place for you, I will come again, and will receive you to myself; that where I am, you may be there also. <u>4</u>Where I go, you know, and you know the way.</h2><p>   <u>5</u>Thomas said to him,</p><h1>THOMAS</h1><h3>Lord, we don’t know where you are going. How can we know the way?</h3><p>   <u>6</u>Jesus said to him,</p><h1>JESUS</h1><h2>I am the way, the truth, and the life. No one comes to the Father, except through me. <u>7</u>If you had known me, you would have known my Father also. From now on, you know him, and have seen him.</h2><p>   <u>8</u>Philip said to him,</p><h1>PHILIP THE DISCIPLE</h1><h3>Lord, show us the Father, and that will be enough for us.</h3><p>   <u>9</u>Jesus said to him,</p><h1>JESUS</h1><h2>Have I been with you such a long time, and do you not know me, Philip? He who has seen me has seen the Father. How do you say, ‘Show us the Father?’ <u>10</u>Don’t you believe that I am in the Father, and the Father in me? The words that I tell you, I speak not from myself; but the Father who lives in me does his works. <u>11</u>Believe me that I am in the Father, and the Father in me; or else believe me for the very works’ sake. <u>12</u>Most certainly I tell you, he who believes in me, the works that I do, he will do also; and he will do greater works than these, because I am going to my Father. <u>13</u>Whatever you will ask in my name, that will I do, that the Father may be glorified in the Son. <u>14</u>If you will ask anything in my name, I will do it. <u>15</u>If you love me, keep my commandments. <u>16</u>I will pray to the Father, and he will give you another Counselor,* that he may be with you forever,— <u>17</u>the Spirit of truth, whom the world can’t receive; for it doesn’t see him, neither knows him. You know him, for he lives with you, and will be in you. <u>18</u>I will not leave you orphans. I will come to you. <u>19</u>Yet a little while, and the world will see me no more; but you will see me. Because I live, you will live also. <u>20</u>In that day you will know that I am in my Father, and you in me, and I in you. <u>21</u>One who has my commandments, and keeps them, that person is one who loves me. One who loves me will be loved by my Father, and I will love him, and will reveal myself to him.</h2><p>   <u>22</u>Judas (not Iscariot) said to him,</p><h1>JUDAS-THADDAEUS</h1><h3>Lord, what has happened that you are about to reveal yourself to us, and not to the world?</h3><p>   <u>23</u>Jesus answered him,</p><h1>JESUS</h1><h2>If a man loves me, he will keep my word. My Father will love him, and we will come to him, and make our home with him. <u>24</u>He who doesn’t love me doesn’t keep my words. The word which you hear isn’t mine, but the Father’s who sent me. <u>25</u>I have said these things to you, while still living with you. <u>26</u>But the Counselor, the Holy Spirit, whom the Father will send in my name, he will teach you all things, and will remind you of all that I said to you. <u>27</u>Peace I leave with you. My peace I give to you; not as the world gives, give I to you. Don’t let your heart be troubled, neither let it be fearful. <u>28</u>You heard how I told you, ‘I go away, and I come to you.’ If you loved me, you would have rejoiced, because I said ‘I am going to my Father;’ for the Father is greater than I. <u>29</u>Now I have told you before it happens so that, when it happens, you may believe. <u>30</u>I will no more speak much with you, for the prince of the world comes, and he has nothing in me. <u>31</u>But that the world may know that I love the Father, and as the Father commanded me, even so I do. Arise, let us go from here.</h2><h5>*John 13:18 Psalm 41:9 <br />*John 14:16 Greek παρακλητον: Counselor, Helper, Intercessor, Advocate, and Comforter.</h5>","path":"/347","Question1":"In this story is there an example that you should imitate? What is it?","Question2":"What steps can you take today to do this?","Question3":"In this story is there an example that you should avoid? What is it?","Question4":"What steps can you take today to do this?","prevPath":"/346","nextPath":"/348"}},"pathContext":{}}

